"use client"

import { useEffect, useState, useCallback, useRef } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import Image from "next/image"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Tabs, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { 
  ChevronLeft, Send, User, MessageSquare, 
  LogOut, Loader2, Sparkles, Hash, Plus, Search, MoreVertical, Settings
} from "lucide-react"

import { Client } from '@stomp/stompjs'
import SockJS from 'sockjs-client'

export default function RealTimeChatPage() {
  const router = useRouter()
  
  const [userId, setUserId] = useState("")
  const [userName, setUserName] = useState("")
  const [activeTab, setActiveTab] = useState("GROUP") 
  const [rooms, setRooms] = useState<any[]>([])
  const [searchQuery, setSearchQuery] = useState("")
  const [currentRoom, setCurrentRoom] = useState<any>(null)
  const [messages, setMessages] = useState<any[]>([])
  const [inputValue, setInputValue] = useState("")
  const [isConnected, setIsConnected] = useState(false)
  const [isLoadingHistory, setIsLoadingHistory] = useState(false)
  
  const stompClient = useRef<Client | null>(null)
  const scrollRef = useRef<HTMLDivElement>(null)
  const subscriptionRef = useRef<any>(null)

  const API_BASE = 'http://sparta-nextme.xyz:18080'

  const loadRooms = useCallback(async (type: string) => {
    const token = localStorage.getItem("accessToken")
    if (!token) return
    try {
      const res = await fetch(`${API_BASE}/v1/chats/room?roomType=${type}`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      const roomList = data.rooms || data.result || (Array.isArray(data) ? data : [])
      setRooms(roomList)
    } catch (e) { setRooms([]) }
  }, [])

  const loadChatHistory = async (roomId: string) => {
    const token = localStorage.getItem("accessToken")
    setIsLoadingHistory(true)
    try {
      const res = await fetch(`${API_BASE}/v1/chats/message/${roomId}?size=50`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      const rawMessages = Array.isArray(data) ? data : (data.messages || data.result || [])
      setMessages([...rawMessages].reverse())
    } catch (e) { setMessages([]) } finally { 
      setIsLoadingHistory(false)
      scrollToBottom()
    }
  }

  const selectRoom = async (room: any) => {
    const roomId = room.chatRoomId || room.id
    if (currentRoom?.chatRoomId === roomId || currentRoom?.id === roomId) return
    const token = localStorage.getItem("accessToken")

    try {
      const joinRes = await fetch(`${API_BASE}/v1/chats/room/${roomId}/join`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json', 'Authorization': `Bearer ${token}` }
      })

      if (joinRes.ok) {
        if (subscriptionRef.current) subscriptionRef.current.unsubscribe()
        setCurrentRoom(room)
        loadChatHistory(roomId)
        if (stompClient.current?.connected) {
          subscriptionRef.current = stompClient.current.subscribe(`/topic/chat.room.${roomId}`, (msg) => {
            const received = JSON.parse(msg.body)
            setMessages((prev) => [...prev, received])
            scrollToBottom()
          })
        }
      }
    } catch (e) { console.error(e) }
  }

  const handleSendMessage = (e?: React.FormEvent) => {
    if (e) e.preventDefault()
    if (!inputValue.trim() || !stompClient.current?.connected || !currentRoom) return
    const roomId = currentRoom.chatRoomId || currentRoom.id
    stompClient.current.publish({
      destination: `/app/chat.send/${roomId}`,
      body: JSON.stringify({ content: inputValue, roomType: currentRoom.roomType || activeTab }),
    })
    setInputValue("")
    scrollToBottom()
  }

  useEffect(() => {
    const token = localStorage.getItem("accessToken")
    if (!token) return router.push("/login")
    setUserId(localStorage.getItem("userId") || "")
    setUserName(localStorage.getItem("name") || "사용자")

    const client = new Client({
      brokerURL: `ws://sparta-nextme.xyz:18080/ws/chat`,
      connectHeaders: { Authorization: `Bearer ${token}` },
      webSocketFactory: () => new SockJS(`${API_BASE}/ws/chat`),
      reconnectDelay: 5000,
      onConnect: () => { setIsConnected(true); loadRooms(activeTab); },
      onDisconnect: () => setIsConnected(false)
    })
    client.activate()
    stompClient.current = client
    return () => { if (stompClient.current) stompClient.current.deactivate() }
  }, [activeTab, loadRooms, router])

  const scrollToBottom = () => {
    setTimeout(() => { if (scrollRef.current) scrollRef.current.scrollTop = scrollRef.current.scrollHeight }, 100)
  }

  return (
    <div className="flex h-screen w-screen bg-[#f8fafc] overflow-hidden font-sans">
      {/* 1. 사이드바 (채널 리스트) */}
      <aside className="w-80 md:w-96 flex flex-col bg-slate-900 border-r border-slate-800 shadow-2xl">
        <div className="p-6 space-y-6">
          <div className="flex items-center justify-between">
            <Link href="/dashboard" className="transition-opacity hover:opacity-80">
               <Image src="/logo.png" alt="Next Me" width={110} height={30} className="h-7 w-auto invert" priority />
            </Link>
            <Button variant="ghost" size="icon" className="h-8 w-8 rounded-lg bg-white/10 text-white hover:bg-white/20">
              <Plus className="h-4 w-4" />
            </Button>
          </div>

          <div className="relative group">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-slate-500 group-focus-within:text-primary transition-colors" />
            <input 
              placeholder="대화방 검색..." 
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="w-full pl-10 h-11 text-sm bg-slate-800/50 border border-slate-700 text-white rounded-xl outline-none focus:border-primary focus:ring-1 focus:ring-primary transition-all" 
            />
          </div>

          <Tabs value={activeTab} onValueChange={(v) => { setActiveTab(v); setCurrentRoom(null); }}>
            <TabsList className="grid grid-cols-2 w-full bg-slate-800 p-1 h-11 rounded-xl">
              <TabsTrigger value="GROUP" className="rounded-lg font-bold text-xs text-slate-400 data-[state=active]:bg-slate-700 data-[state=active]:text-white">그룹 채널</TabsTrigger>
              <TabsTrigger value="DIRECT" className="rounded-lg font-bold text-xs text-slate-400 data-[state=active]:bg-slate-700 data-[state=active]:text-white">1:1 메시지</TabsTrigger>
            </TabsList>
          </Tabs>
        </div>

        <div className="flex-1 overflow-y-auto px-3 pb-6 space-y-1 custom-scrollbar">
          {rooms.filter(r => (r.title || r.name || "").toLowerCase().includes(searchQuery.toLowerCase())).map((room: any) => (
            <button
              key={room.chatRoomId || room.id}
              onClick={() => selectRoom(room)}
              className={`w-full p-4 rounded-xl flex items-center gap-4 transition-all ${
                (currentRoom?.chatRoomId || currentRoom?.id) === (room.chatRoomId || room.id) 
                ? 'bg-primary text-white shadow-lg shadow-primary/20' 
                : 'text-slate-400 hover:bg-slate-800/50 hover:text-slate-200'
              }`}
            >
              <div className={`h-11 w-11 rounded-xl flex items-center justify-center shrink-0 border border-slate-700 ${
                (currentRoom?.chatRoomId || currentRoom?.id) === (room.chatRoomId || room.id) ? 'bg-white/20 border-white/30' : 'bg-slate-800'
              }`}>
                {activeTab === "GROUP" ? <Hash className="h-5 w-5" /> : <User className="h-5 w-5" />}
              </div>
              <div className="text-left overflow-hidden flex-1">
                <p className="font-bold truncate text-[14px] leading-tight mb-1">{room.title || room.name}</p>
                <p className={`text-[11px] truncate opacity-70`}>{room.lastMessage || "새로운 메시지가 없습니다."}</p>
              </div>
            </button>
          ))}
        </div>

        <div className="p-4 bg-slate-800/30 border-t border-slate-800">
           <div className="flex items-center gap-3">
              <Avatar className="h-9 w-9 border border-slate-700 shadow-sm">
                <AvatarFallback className="bg-slate-700 text-slate-300 font-bold">{userName?.charAt(0)}</AvatarFallback>
              </Avatar>
              <div className="flex-1">
                <p className="text-[13px] font-bold text-slate-200 leading-none mb-1">{userName}님</p>
                <div className="flex items-center gap-1.5">
                  <div className={`h-1.5 w-1.5 rounded-full ${isConnected ? 'bg-green-500 shadow-[0_0_5px_rgba(34,197,94,0.5)]' : 'bg-slate-500'}`} />
                  <span className="text-[10px] text-slate-500 font-bold uppercase">{isConnected ? "Online" : "Offline"}</span>
                </div>
              </div>
              <Button variant="ghost" size="icon" onClick={() => router.push("/dashboard/settings")} className="h-8 w-8 text-slate-500 hover:text-slate-200 hover:bg-slate-800"><Settings className="h-4 w-4" /></Button>
           </div>
        </div>
      </aside>

      {/* 2. 메인 채팅 창 */}
      <main className="flex-1 flex flex-col bg-white relative">
        {currentRoom ? (
          <>
            {/* Header */}
            <header className="h-[72px] px-8 border-b border-slate-100 flex items-center justify-between shrink-0 bg-white/80 backdrop-blur-md z-10">
              <div className="flex items-center gap-4">
                <div className="h-10 w-10 rounded-xl bg-slate-100 flex items-center justify-center text-slate-600">
                  {activeTab === "GROUP" ? <Hash className="h-5 w-5" /> : <User className="h-5 w-5" />}
                </div>
                <div>
                  <h2 className="font-bold text-base text-slate-900 tracking-tight">{currentRoom.title || currentRoom.name}</h2>
                  <div className="flex items-center gap-1.5">
                    <div className="h-1.5 w-1.5 rounded-full bg-green-500" />
                    <span className="text-[11px] text-slate-400 font-bold uppercase">실시간 연결됨</span>
                  </div>
                </div>
              </div>
              <div className="flex items-center gap-2">
                <Button variant="ghost" size="sm" onClick={() => {}} className="text-xs font-bold text-slate-500 hover:text-destructive">방 나가기</Button>
                <div className="w-[1px] h-4 bg-slate-200 mx-2" />
                <Button variant="ghost" size="icon" className="h-9 w-9 text-slate-400 rounded-lg"><MoreVertical className="h-5 w-5" /></Button>
              </div>
            </header>

            {/* Message Area */}
            <div ref={scrollRef} className="flex-1 overflow-y-auto px-8 py-8 space-y-6 custom-scrollbar bg-[#f8fafc]">
              {isLoadingHistory && <div className="flex justify-center p-4"><Loader2 className="h-6 w-6 animate-spin text-primary" /></div>}
              {messages.map((msg, idx) => {
                const isMine = msg.senderId === userId
                return (
                  <div key={idx} className={`flex ${isMine ? "justify-end" : "justify-start"} animate-in fade-in slide-in-from-bottom-2 duration-300`}>
                    <div className={`flex flex-col space-y-1.5 max-w-[70%] ${isMine ? "items-end" : "items-start"}`}>
                      {!isMine && <span className="text-[11px] font-bold text-slate-500 ml-1">{msg.senderName}</span>}
                      <div className={`p-4 text-sm font-medium leading-relaxed shadow-sm ${
                        isMine 
                        ? "bg-primary text-white rounded-2xl rounded-tr-none shadow-lg shadow-primary/20" 
                        : "bg-white border border-slate-100 text-slate-800 rounded-2xl rounded-tl-none"
                      }`}>
                        {msg.content}
                      </div>
                      <span className="text-[9px] text-slate-400 font-bold px-1 uppercase tracking-tighter">
                        {msg.createdAt ? new Date(msg.createdAt).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' }) : ""}
                      </span>
                    </div>
                  </div>
                )
              })}
            </div>

            {/* Input Area (Full Width Card Style) */}
            <footer className="p-6 bg-white border-t border-slate-100">
              <form onSubmit={handleSendMessage} className="max-w-5xl mx-auto flex gap-3 relative items-center">
                <div className="flex-1 relative">
                  <Input 
                    value={inputValue}
                    onChange={(e) => setInputValue(e.target.value)}
                    placeholder="메시지를 입력해 주세요..." 
                    className="w-full h-14 bg-slate-100 border-none rounded-2xl pl-6 pr-14 font-bold text-sm focus:ring-4 focus:ring-primary/10 transition-all placeholder:text-slate-400"
                  />
                  <Button 
                    type="submit" 
                    size="icon" 
                    className="absolute right-2 top-2 h-10 w-10 rounded-xl bg-primary text-white shadow-lg shadow-primary/20 hover:scale-[1.02] active:scale-95 transition-all" 
                    disabled={!inputValue.trim()}
                  >
                    <Send className="h-5 w-5" />
                  </Button>
                </div>
              </form>
            </footer >
          </>
        ) : (
          /* Empty State */
          <div className="flex-1 flex flex-col items-center justify-center p-12 text-center bg-[#f8fafc]">
            <div className="w-20 h-20 rounded-[2.5rem] bg-white flex items-center justify-center mb-8 shadow-xl shadow-slate-200/50 border border-slate-50">
              <MessageSquare className="h-9 w-9 text-slate-300 animate-pulse" />
            </div>
            <h3 className="text-xl font-black text-slate-800 tracking-tight">대화방을 선택해 주세요</h3>
            <p className="text-sm text-slate-400 mt-3 max-w-[280px] font-medium leading-relaxed">
              왼쪽의 채널 리스트에서 참여할 방을 선택하여<br/>새로운 대화를 시작하실 수 있습니다.
            </p>
          </div>
        )}
      </main>

      <style jsx global>{`
        .custom-scrollbar::-webkit-scrollbar { width: 5px; }
        .custom-scrollbar::-webkit-scrollbar-track { background: transparent; }
        .custom-scrollbar::-webkit-scrollbar-thumb { background: #cbd5e1; border-radius: 10px; }
        .custom-scrollbar::-webkit-scrollbar-thumb:hover { background: #94a3b8; }
      `}</style>
    </div>
  )
}